.qHeader{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1px;
    background-color: aqua;
    width: 100%;
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: black;

}

.qHeader__logo > img {
    height: 30px;
    margin-right: 20px;
}

.qHeader__icons{
    display: flex;
    margin-right: 20px;
}

.qHeader__icon > .MuiSvgIcon-root{
    color: rgb(88, 2, 2);
    cursor: pointer;
    font-size: xx-large;
    padding: 5px 20px;
}

.qHeader__icon > .MuiSvgIcon-root:hover{
    color: chartreuse;
    border-radius: 10px;
    background-color: cornsilk;
}

.qHeader__input{
    display: flex;
    align-items: center;
    border: darkgray;
    padding: 5px;
    border-radius: 5px;

}

.qHeader__input > input{
    background-color: transparent;
    outline: none;
    border: none;
    color: gray;
}

.qHeader__input > .MuiSvgIcon-root{ 
    color: gray;
}
 
.qHeader__Rem {
    display: flex;
    align-items: center;
    margin-left: 25px;
}

.qHeader__Rem > .qHeader__avater{
    cursor: pointer;
}

.qHeader__Rem > .MuiSvgIcon-root{
    font-size: xx-large;
    color: gray;
    margin-left: 25px;
    cursor: pointer;
}

.qHeader__Rem > .MuiSvgIcon-root:hover {

    color: rgb(238, 11, 143);
}

.qHeader__Rem > .MuiButton-root{
    color: rgb(7, 241, 65);
    margin-left: 25px;
    text-transform: inherit;
    background: lightcoral;
    border-radius: 20px;
    cursor: pointer;
}

.qHeader__Rem > .MuiButton-root:hover{
    color: rgb(209, 223, 213);
    background-color: lightcoral;   
}

.qHeader__try {
    font-size: 50px;
    text-align: left;
    color: red;
    border: darkgray;
    padding: 5px;
    border-radius: 10px;
}

.modal_title {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(187, 187, 187,0.5);
}

.modal_title > h5 {
    color: gray;
    font-size: 20px;
    cursor: pointer;
    font-weight: 800;
    margin-right: 30px;
 
}

.modal_title > h5:hover {
    color: red;
}

.modal_info {
    display: flex;
    align-items: left;
    margin-top: 30px;
    
}

.modal_info > p {
    margin-left: 10px;
    font-size: small;
    color: gray;
}

.modal_info > .modal_scope {
    display: flex;
    align-items: center;
    color: gray;
    padding: 5px;
    margin-left: 10px;
    background-color: honeydew;
    border-radius: 33px;
    cursor: pointer;
}

.modal_field {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    flex: 1; 
}

.modal_field > .modal_fieldLink {
    color: gray;
    display: flex;
    margin-top: 10px;
    align-items: center;
}

.modal_field > .modal_fieldLink > input {
    flex: 1;
    border: none;
    outline: none;
    margin-left: 5px;
}

.modal_buttons {
    display: flex;
    align-items: center;
    margin-top: 260px;
    margin-right: -20px;
    border-top: 1px solid lightgreen;
}

.modal_buttons > .cancle {
    margin-left: auto;
    border: none;
    margin-top: 5px;
    outline: none;
    color: gray;
    font-weight: 500;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
}

.modal_buttons > .add {
    border: none;
    outline: none;
    margin-top: 5px;
    background-color: darkorchid;
    color: floralwhite;
    font-weight: 700;
    padding: 10px;
    margin-right: 10px;
    border-radius: 33px;
    cursor: pointer;
}
