.quoraBox {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: black;
    background-color: blanchedalmond;
    border-radius: 5px;
    cursor: pointer;
}

.quoraBox:hover {
    border-color: rgb(153, 0, 255);
}

.quoraBox__info {
    display: flex;
    align-items: center;
}

.quoraBox__info > h5 {
    color: rgb(19, 129, 226);
    font-weight: 300;
    margin-left: 10px;
}

.quoraBox__quora {
    display: flex;
    margin-top: 8px;
}

.quoraBox__quora > p {
    color: coral;
    font-weight: bold;
    font-size: large;
}