.sidebarOption {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

.sidebarOption:hover {
background-color: light gray;
border-radius: 5px;
}

.sidebarOption > img {
    height: 30px;
    margin-left: 20px;
    border-radius: 5px;
    }

